import React from 'react';
import WarningIcon from '../svg/WarningIcon';
import send from '../../assets/imgs/send.svg';
import MyMessage from './MyMessage';
import UserMessage from './UserMessage';
const Chat = () => {
  return (
    <div className="chat">
      <div className="chat-count">
        <div className='fs-13'>102</div>
        <div className='fs-09'> online</div>
      </div>
      <div className="chat-window">
        <UserMessage name={'User8name'} time={'12:36'} text={'    Marvel Future Fight   '}/>
        <UserMessage name={'Galadriel_90'} time={'12:37'} text={'  .   .   .'}/>
        <MyMessage name={'Weatherwax'} time={'12:37'} text={'!   '}/>
        <UserMessage name={'Galadriel_90'} time={'12:37'} text={'-          '}/>
        <UserMessage name={'User8name'} time={'12:40'} text={'    Marvel Future Fight   '}/>
        <MyMessage name={'Weatherwax'} time={'12:37'} text={'!   '}/>
        <UserMessage name={'User8name'} time={'12:36'} text={'    Marvel Future Fight   '}/>
        <UserMessage name={'Galadriel_90'} time={'12:37'} text={'  .   .   .'}/>
      </div>
      <form action="" className='chat-form'>
        <div className="input">
          <input type="text" placeholder='   '/>
        </div>
        <button type='submit' className='btn-1'>
          <img src={send} alt="send" />
        </button>
      </form>
      <button type='button' className='chat-report'>
        <WarningIcon/>
      </button>
    </div>
  );
};

export default Chat;